import React from "react";
import { destroyUser } from "../redux/User/user.actions"
import { connect } from "react-redux"
import { useNavigate } from "react-router-dom";
import slideImg1 from '../public/header-logo.png';
import slideImg2 from '../public/US-flag.png';
import slideImg3 from '../public/russia.png';
import slideImg4 from '../public/chefs-images-1.png';

function Header(props) {
let navigate = useNavigate();
const logout = async e => {
localStorage.clear();
props.destroyUser();
navigate("/login");
}
return (

<div class="">
  <div class="container header-dropdown">
    <nav class="navbar navbar-expand-lg d-flex custom-border">
      <a class="navbar-brand me-0" href="/cookreact">
        <img src={slideImg1} class="img-fluid header-logo" />
      </a>

      <div class="d-flex align-items-center order-lg-2">
        <ul class=" d-flex ps-0 align-items-center mb-0 navbar custom-header">

          <li class="nav-item me-1 ">
            <a class=" text-color fs-7 d-md-block d-none" aria-current="page" href="#"><i
                class="bi bi-chat-square-text d-flex aling-items-center"><i class="bi bi-circle-fill chat-notification-icon text-success"></i></i></a>
          </li>

          <div class="dropdown me-md-4">
            <button class="btn dropdown-toggle text-color fs-7 p-0 mx-md-2" type="button" id="dropdownMenuButton1"
              data-bs-toggle="dropdown" aria-expanded="false">
              <img src={slideImg2} class="img-fluid us-flag" /> <span
                class="d-md-inline-block d-none">En</span></button>


            <ul class="dropdown-menu dropdown-menu-end" aria-labelledby="dropdownMenuButton1">
              <li><a class="dropdown-item ps-2" href="#"><img src={slideImg2}
                    class="img-fluid us-flag me-1" />English</a></li>

              <li><a class="dropdown-item ps-2" href="#"><img src={slideImg3}
                    class="img-fluid russian-flag me-1" />Russian</a></li>
            </ul>
          </div>


          <div class="dropdown d-none">
            <button class="btn dropdown-toggle text-color fs-6 p-0  mx-md-2" type="button" id="dropdownMenuButton1"
              data-bs-toggle="dropdown" aria-expanded="false">
              <i class="bi bi-person-circle"></i> <span class="d-md-inline-block d-none">Mohd</span></button>


            <ul class="dropdown-menu dropdown-menu-end" aria-labelledby="dropdownMenuButton1">
              <li><a class="dropdown-item ps-2" href="#">English</a></li>

              <li><a class="dropdown-item ps-2" href="#">Russian</a></li>
            </ul>
          </div>


          {props.user == null ?
          (<div class="d-flex align-items-center">
            <li class="nav-item me-3 me-md-4">
              <a class=" text-color fs-6 d-md-block d-none" aria-current="page" href="/cookreact/signup">Sign up</a>
            </li>
            <form class="d-flex">
              <a class="btn btn-success fs-6 button-shadow d-md-block d-none" type="submit" href="/cookreact/login">Sign In</a>
              <a class="text-color fs-7 fs-7 d-md-none d-block" type="submit" href="/cookreact/login"><i
                  class="bi bi-person-circle sign-in-icon me-3"></i></a>
            </form>
          </div>) :


          <div class="dropdown admin-dropdown d-flex align-items-center me-md-0 ">
            <div class="login-user-img">
           <img src={slideImg4} class="img-fluid" />
            </div>
            <a class="btn dropdown-toggle text-capitalize d-flex aling-items-center" href="#" role="button" id="dropdownMenuLink"
              data-bs-toggle="dropdown" aria-expanded="false">
            <span class="login-user-name d-md-block d-none">  {props.user.firstname }</span>
            </a>

            <ul class="dropdown-menu dropdown-menu-end" aria-labelledby="dropdownMenuLink">
              <li><a class="dropdown-item" href="/cookreact/conversations">Messages</a></li>
              <li><a class="dropdown-item" href="/cookreact/account-edit">Account Edit</a></li>
              <li><a class="dropdown-item" href="/cookreact/complete-profile">Complete Profile</a></li>
              <li>
                <a onClick={()=> logout()} class="btn fs-6 pt-0 d-md-inline-block dropdown-item ps-md-3 ps-3"
                  type="button" href="#">Logout</a>
              </li>
            </ul>
          </div>
          }

        </ul>

        <button class="navbar-toggler p-0" type="button" data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
          aria-label="Toggle navigation">
          <span class="navbar-toggler-icon h-100 w-100"><i class="bi bi-list"></i></span>
        </button>
      </div>

      <div class="collapse navbar-collapse order-md-1" id="navbarSupportedContent">
        <ul class="navbar-nav me-auto mb-lg-0 ms-md-5 ms-0">
          <li class="nav-item me-md-2 me-0">
            <a class="nav-link active text-color fs-6 py-1" aria-current="page" href="/cookreact">Home</a>
          </li>

          <li class="nav-item me-md-2 me-0">
            <a class="nav-link text-color fs-6 py-1" aria-current="page" href="/cookreact/about-us">About</a>
          </li>

          <li class="nav-item me-md-2 me-0">
            <a class="nav-link text-color fs-6 py-1" aria-current="page" href="/cookreact/cook-list">Top Cooks</a>
          </li>
          <li class="nav-item me-md-2 me-0">
            <a class="nav-link text-color fs-6 py-1" aria-current="page" href="/cookreact/map-view">Contact us</a>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</div>

);
}

const mapStateToProps = state => {
return {
user: state.user.user
}
}
const mapDispatchToProps = dispatch => {
return {
destroyUser: () => dispatch(destroyUser())
}
}
export default connect(mapStateToProps, mapDispatchToProps)(Header)