import React from "react";
import slideImg1 from '../public/chat-img2.png';
import slideImg2 from '../public/best-recipe1.jpg';

function RecipeDetail() {
return (

<div class="container">
  <div class="row">
    <div class="col-lg-8 order-lg-1 order-2">
      <h4 class="heading-color fw-bold mt-4 mb-3">Lemony Tortellini Soup with Spinach and Dill</h4>
      <div class="d-flex align-items-center mb-5">
        <div class="recipe-details-chef me-2">
          <img src={slideImg1} class="img-fluid" />
        </div>
        <p class="mb-0 text-color fw-bold">By Jane smith</p>
      </div>

      <h4 class="heading-color fw-bold mb-0">WHY THIS RECIPE WORKS</h4>
      <hr class="text-success" />
      <p class="text-color">The broilers direct, intense heat is great for browning, but shrimp are so small that its
        hard to get color
        on them before they overcook. We started by briefly salting the shrimp so that they retained moisture even as
        they were cooked under high heat. A coating of butter and honey added richness, boosted browning, and
        underscored the shrimps sweetness. To cook, we arranged the shrimp in a single layer on a wire rack set in a
        rimmed baking sheet to allow for airflow, which promotes even cooking. Setting the oven rack 4 inches from the
        broiler ensured the shrimp had enough distance to cook evenly and brown quickly without needing to be turned.
        Chili powder and cayenne pepper were easily stirred directly into the honey-butter mixture before cooking,
        giving the shrimp a sweet-spicy profile perfectly balanced by a shower of fresh, thinly sliced scallions.</p>

      <div class="mt-5 Ingredients-wrapper">
        <h5 class="heading-color fw-bold mb-0">INGREDIENTS</h5>
        <hr class="text-success" />
        <div class="row">
          <div class="col-md-6">
            <ul>
              <li>1  pounds extra-large shrimp (21 to 25 per pound), peeled and deveined, tails left on</li>
              <li> teaspoon kosher salt</li>
              <li>4 tablespoons unsalted butter</li>
              <li>2 tablespoons honey</li>
              <li> teaspoon chili powder</li>
              <li> teaspoon cayenne pepper</li>
            </ul>
          </div>
          <div class="col-md-6">
            <ul>
              <li>2 scallions, sliced thin</li>
              <li>Lemon wedges</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="col-lg-4 order-lg-2 order-1 recipe-detail-img-section">
      <img src={slideImg2} class="img-fluid recipe-detail-img" />

      <div class="my-5 cooking-timing d-flex align-items-center justify-content-center">
        <div class="">
          <p class="fw-bold">Time: <span class="fw-normal"> 45 mins</span></p>
          <p class="fw-bold">Dietary preference: <span class="fw-normal"> Vegetarian</span></p>
          <p class="fw-bold mb-0">Recipe category: <span class="fw-normal"> Italian </span></p>
        </div>
      </div>
    </div>
  </div>
</div>


);
}

export default RecipeDetail;