import React from "react";

function Contactus() {
return (
<div>
    <div class="container">
        <div class="row justify-content-center">
            <div class="col-md-8">
                <div class="row">
                    <div class="col-md-12">
                        <div class="contact-us-wrapper">
                            <h2 class="heading-color fw-bold my-5">Contact us</h2>
                        </div>
                        <form>
                            <div class="row">
                                <div class="col-md-6">
                                    <div class="mb-3">
                                        <label for="name" class="form-label heading-color fw-bold">Name</label>
                                        <input type="text" class="form-control" placeholder="Enter name" id="name" />
                                    </div>
                                </div>

                                <div class="col-md-6">
                                <div class="mb-3">
                                    <label for="exampleInputEmail1" class="form-label heading-color fw-bold">Email</label>
                                    <input type="email" class="form-control" placeholder="Enter email" id="exampleInputEmail1" />
                                  </div>
                                </div>

                              <div class="col-md-12">
                                <div class="mb-3">
                                    <label for="subject" class="form-label heading-color fw-bold">Subject</label>
                                    <input type="text" class="form-control" placeholder="Enter subject" id="subject" />
                                </div>
                              </div>

                              <div class="col-md-12">
                                <div class="mb-3">
                                    <label for="exampleFormControlTextarea1" class="form-label heading-color fw-bold">Message</label>
                                    <textarea class="form-control" id="exampleFormControlTextarea1" placeholder="Enter message" rows="5"></textarea>
                                  </div>
                              </div>

                              <div class="text-end mb-5 mt-4">
                                <button type="submit" class="btn btn-success">Submit</button>
                              </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
);
}

export default Contactus;