import React from "react";


function ForgotPassword() {
return (
<div>
    <div class="container">
        <div class="row justify-content-center">
            <div class="col-md-6">
                <div class="row justify-content-center">
                    <div class="col-md-10">
                        <div class="forgot-password-wrapper">
                            <h3 class="heading-color fw-bold mt-5">Forgot Password</h3>
                            <form>
                                <div class="mb-3 mt-4">
                                    <div class="input-group">
                                        <input type="email" name="email" class="form-control"
                                            placeholder="Email address" aria-label="email"
                                            aria-describedby="basic-addon1" />
                                        <span class="input-group-text" id="basic-addon1"><i
                                                class="bi bi-envelope-fill text-color"></i></span>
                                    </div>
                                </div>

                                <div class="reset-password-alert">
                                    <button type="submit" class="btn btn-success w-100 mt-2 mb-5 py-2"
                                        data-bs-toggle="modal" data-bs-target="#exampleModal">Send Link </button>

                                    <div class="modal fade" id="exampleModal" tabindex="-1"
                                        aria-labelledby="exampleModalLabel" aria-hidden="true">
                                        <div class="modal-dialog">
                                            <div class="modal-content">
                                                <div class="modal-header border-0 alert alert-success mb-0">
                                                    <h6 class="modal-title " id="exampleModalLabel"><i class="bi bi-check-circle-fill"></i> Please check your
                                                        email</h6>
                                                    <button type="button" class="btn-close" data-bs-dismiss="modal"
                                                        aria-label="Close"></button>
                                                </div>

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>

)
};

export default ForgotPassword;