import React from "react";

function UpdatePassword() {
    return (

        <div>
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <hr class="light-gray mt-3" />
                        <h2 class="heading-color fw-bold mt-4">Update Password</h2>
                        <form>
                            <div class="row">
                                <div class="col-md-4">
                                    <div class="my-3">
                                        <label htmlFor="password" class="form-label heading-color fw-bold">Current password</label>
                                        <input type="password" class="form-control" id="password" placeholder="Enter"
                                            name="password" />
                                    </div>
                                </div>

                                <div class="col-md-4">
                                    <div class="my-3">
                                        <label htmlFor="new-password" class="form-label heading-color fw-bold">New password</label>
                                        <input type="password" class="form-control" id="new-password" placeholder="Enter"
                                            name="password" />
                                    </div>
                                </div>

                                <div class="col-md-4">
                                    <div class="my-3">
                                        <label htmlFor="confirm-password" class="form-label heading-color fw-bold">Confirm new
                                            password</label>
                                        <input type="password" class="form-control" id="confirm-password" placeholder="Enter"
                                            name="password" />
                                    </div>
                                </div>

                                <div class="text-end account-details-save mb-5 mt-3">
                                    <button type="submit" class="btn btn-success">Save</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default UpdatePassword;