import React from "react";
import Filters from "../cook-listing/filters";
import { connect } from "react-redux"
function MapView(props) {
return (
<div>
    <div class="container">
        <div class="row">
            <div class="col-md-3">
                <Filters />
            </div>
            <div class="col-md-9">
                <div class="listing-heading d-md-flex d-inline-block align-items-center justify-content-between">
                    <h5 class="fw-bold heading-color my-3">{props?.cooks?.length} items found</h5>
                    <div class="d-flex align-items-center sort-by-wrapper">

                        <label for="select" class="form-label w-100 mb-0 heading-color fw-bold">Sort by:</label>
                        <select class="form-select me-2 text-color sort-by-list" aria-label="Default select example"
                            id="select">
                            <option selected>Price</option>
                            <option value="1">One</option>
                            <option value="2">Two</option>
                            <option value="3">Three</option>
                        </select>
                        <select class="form-select me-2 text-color sort-by-list" aria-label="Default select example"
                            id="select">
                            <option selected>Distance</option>
                            <option value="1">One</option>
                            <option value="2">Two</option>
                            <option value="3">Three</option>
                        </select>

                        <a href="/cookreact/cook-list" class="pin-map-icon me-2 d-flex align-items-center justify-content-center">
                            <i class="bi bi-list-ul"></i>
                        </a>
                        <a href="#" class="list-icon d-flex align-items-center justify-content-center">
                            <i class="bi bi-pin-map-fill "></i>
                        </a>
                    </div>
                </div>
                <div class="ratio ratio-4x3 mt-3 mt-md-0 mb-5 mb-md-0">
                    <iframe
                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d109740.86108784034!2d76.69348820329913!3d30.73521019973123!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x390fed0be66ec96b%3A0xa5ff67f9527319fe!2sChandigarh!5e0!3m2!1sen!2sin!4v1643351294974!5m2!1sen!2sin"
                        allowfullscreen="" loading="lazy"></iframe>
                </div>
            </div>
        </div>
    </div>
</div>
);
}
const mapStateToProps = state => {
  return {
    cooks: state.cooks.cooks
  }
}
export default connect(mapStateToProps)(MapView)