import React from "react";
import slideImg1 from '../public/chefs-images-1.png';
import Filters from "./filters";
import { Link } from "react-router-dom";
import { connect } from "react-redux"
function CookListing(props) {
return (
<div>
  <div class="container">
    <hr class="mt-0" />
    <div class="row">
      <div class="col-md-3">
        <Filters />
      </div>

      <div class="col-md-9">
        <div class="listing-heading d-md-flex d-inline-block align-items-center justify-content-between w-100">
          <div class="d-flex align-items-center justify-content-between my-3 my-md-0">
            <h5 class="fw-bold heading-color my-3">{props?.cooks?.length} items found</h5>
            <div class="d-flex d-md-none d-block">
              <a href="/cookreact/cook-list" class="list-icon me-2 d-flex align-items-center justify-content-center">
                <i class="bi bi-list-ul"></i>
              </a>
              <a href="/cookreact/map-view" class="pin-map-icon d-flex align-items-center justify-content-center">
                <i class="bi bi-pin-map-fill text-color"></i>
              </a>
            </div>
          </div>
          <div class="d-flex align-items-center sort-by-wrapper">
            <label htmlFor="select" defaultValue="" class="form-label w-100 mb-0 heading-color fw-bold">Sort by:</label>
            <select class="form-select me-2 text-color sort-by-list" aria-label="Default select example" id="select">
              <option value="">Price</option>
              <option value="1">One</option>
              <option value="2">Two</option>
              <option value="3">Three</option>
            </select>

            <select class="form-select me-2 text-color sort-by-list" defaultValue='' aria-label="Default select example"
              id="select">
              <option value=''>Distance</option>
              <option value="1">One</option>
              <option value="2">Two</option>
              <option value="3">Three</option>
            </select>

            <div class="d-md-flex d-none">
              <a href="/cookreact/cook-list" class="list-icon me-2 d-flex align-items-center justify-content-center">
                <i class="bi bi-list-ul"></i>
              </a>
              <a href="/cookreact/map-view" class="pin-map-icon d-flex align-items-center justify-content-center">
                <i class="bi bi-pin-map-fill text-color"></i>
              </a>
            </div>
          </div>
        </div>

        <div class="row mt-3">
          <div class="col-md-12 cook-listing-view">
            {props.cooks.map(cooks => {
            return (
            <div key={cooks.email} class="list-view mb-3">
              <div class="row">
                <div class="col-md-4">
                  <img src={slideImg1} class="img-fluid w-md-auto w-100 chef-details-img" />
                </div>

                <div class="col-md-8">
                  <Link to="/cookreact/cook-details">
                  <h4 class="heading-color fw-bold mb-0 mt-2 mt-md-0">{cooks.firstname + " "+ cooks.lastname}</h4>
                  </Link>
                  <p class="text-color mb-1"> <i class="bi bi-geo-alt-fill me-1"></i>0.5 km
                    away</p>
                  <div class="d-flex align-items-center mt-3">
                    <p class="fs-6 heading-color pe-2 mb-0">4.5</p>
                    <i class="bi bi-star-fill gold-color gold-icons me-1"></i>
                    <i class="bi bi-star-fill gold-color gold-icons me-1"></i>
                    <i class="bi bi-star-fill gold-color gold-icons me-1"></i>
                    <i class="bi bi-star-fill gold-color gold-icons me-1"></i>
                    <i class="bi bi-star-half gold-color gold-icons me-1"></i>
                  </div>

                  <div class="d-md-flex d-inline-block align-items-center justify-content-between">
                    <p class="text-color mt-1 list-view-info mb-0">Speciality: Pudding, Beef
                      Skewers, Ice Cream, Pinot Grigio</p>
                    <h3 class="text-success fw-bold prize mb-0 mt-md-0 mt-2">$36<span class="light-gray">/hr</span></h3>
                  </div>

                  <div class="mt-md-3 mt-2 d-md-flex d-block align-items-center justify-content-between">
                    <p class="light-gray mb-0"><i class="bi bi-translate me-2 fs-7"></i>English, Spanish</p>
                    <a href="#" class="btn btn-success cook-book-button">Book now</a>
                  </div>
                </div>
              </div>
            </div>
            )
            })}

          </div>
        </div>
      </div>
    </div>
  </div>
</div>
);
}
const mapStateToProps = state => {
return {
cooks: state.cooks.cooks
}
}
export default connect(mapStateToProps)(CookListing)