import React from "react";


function ResetPassword() {
return (
<div>
    <div class="container">
        <div class="container">
            <div class="row justify-content-center">
                <div class="col-md-6">
                    <div class="row justify-content-center">
                        <div class="col-md-10">
                            <div class="forgot-password-wrapper">
                                <h3 class="heading-color fw-bold mt-5">Reset Password</h3>
                                <form>
                                   
                            <div class="mb-3">
                                <div class="input-group">
                                    <input type="password" name="password" class="form-control"
                                        placeholder="New password" aria-label="password"
                                        aria-describedby="basic-addon1" />
                                    <span class="input-group-text" id="basic-addon1"><i
                                            class="bi bi-eye-slash-fill text-color"></i></span>
                                </div>
                            </div>

                            <div class="mb-3">
                                <div class="input-group">
                                    <input type="password" name="password" class="form-control"
                                        placeholder="Confirm new password" aria-label="password"
                                        aria-describedby="basic-addon1" />
                                    <span class="input-group-text" id="basic-addon1"><i
                                            class="bi bi-eye-slash-fill text-color"></i></span>
                                </div>
                            </div>

                                    <button type="submit" class="btn btn-success w-100 mt-2 mb-5 py-2">Save</button>

                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>

)
};

export default ResetPassword;