import React from "react";
import { useForm } from "react-hook-form"
import { connect } from "react-redux"
import { saveUser } from "../redux/User/user.actions"
const { REACT_APP_API_URL } = process.env;

async function loginUser(formData) {
    return fetch(`${REACT_APP_API_URL}api/update/user`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'x-access-token': localStorage.getItem('token'),
        },
        body: JSON.stringify(formData)
    });
}
function AccountDetail(props) {
    const { watch, register, handleSubmit, setError, getValues, setValue, clearErrors, formState: { errors } } = useForm();

    const onSubmit = async e => {
        const token = await loginUser(getValues()).then(data => data.json())
            .then(res => {
                if (res.status == false) {
                    setError("serverError", { type: "focus", message: res.message });
                } else {
                    localStorage.setItem('cuser', JSON.stringify(res.data));
                    props.saveUser(res.data);
                }
            })
    }
    React.useEffect(() => {
        if (props.user) {
            setValue('firstname', props.user.firstname)
            setValue('lastname', props.user.lastname)
            setValue('email', props.user.email)
        }
    }, [props.user]);
    return (

        <div>
            <div class="container">
                <div class="account-details-wrapper">
                <div class="row">
                    <div class="col-md-12">
                        <h2 class="heading-color fw-bold">Account details</h2>
                    </div>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div class="row">
                            <div class="col-md-4">
                                <div class="my-3">
                                    <label htmlFor="fname" class="form-label heading-color fw-bold">First Name</label>
                                    <input type="text" name="firstname" class="form-control" id="fname" placeholder="Enter firstname"
                                        {...register('firstname', { required: true })} />
                                </div>
                                {errors.firstname && errors.firstname.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Firstname is required.</p>
                                )}
                            </div>

                            <div class="col-md-4">
                                <div class="my-3">
                                    <label htmlFor="lname" class="form-label heading-color fw-bold">Last Name</label>
                                    <input type="text" name="lastname" class="form-control" id="lname" placeholder="Enter lastname"
                                        {...register('lastname', { required: true })} />
                                </div>
                                {errors.lastname && errors.lastname.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Lastname is required.</p>
                                )}
                            </div>

                            <div class="col-md-4">
                                <div class="my-3">
                                    <div class="d-flex align-items-center justify-content-between">
                                        <label htmlFor="email" class="form-label heading-color fw-bold">Email</label>
                                        <div class="d-flex align-items-center text-success">
                                            <i class="bi bi-check2-circle me-1"></i>
                                            <p class="mb-0">Verified</p>
                                        </div>
                                    </div>
                                    <input type="email" class="form-control" id="email" placeholder="yourname@gmail.com"
                                        name="email" {...register('email', {
                                            required: true, pattern:
                                                /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,})+$/
                                        })} />
                                </div>
                                {errors.email && errors.email.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Email is required.</p>
                                )}
                                {errors.email && errors.email.type === "pattern" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Email is not valid.</p>
                                )}
                            </div>

                            <div class="col-md-4">
                                <div class="my-3">
                                    <label htmlFor="Country" class="form-label heading-color fw-bold">Country</label>
                                    <select class="form-select" name="country" defaultValue='' aria-label="Default select example" {...register('country', { required: true })}>
                                        <option value="" disabled>Select country</option>
                                        <option value="1">One</option>
                                        <option value="2">Two</option>
                                        <option value="3">Three</option>
                                    </select>
                                </div>
                                {errors.country && errors.country.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Country is required.</p>
                                )}
                            </div>

                            <div class="col-md-4">
                                <div class="my-3">
                                    <label htmlFor="State" class="form-label heading-color fw-bold">State</label>
                                    <select class="form-select" name="state" defaultValue='' aria-label="Default select example" {...register('state', { required: true })}>
                                        <option value="" disabled>Select state</option>
                                        <option value="1">One</option>
                                        <option value="2">Two</option>
                                        <option value="3">Three</option>
                                    </select>
                                </div>
                                {errors.state && errors.state.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">State is required.</p>
                                )}
                            </div>

                            <div class="col-md-4">
                                <div class="my-3">
                                    <label htmlFor="City" class="form-label heading-color fw-bold">City</label>
                                    <select class="form-select" name="city" defaultValue='' aria-label="Default select example" {...register('city', { required: true })}>
                                        <option value="" disabled>Select city</option>
                                        <option value="1">One</option>
                                        <option value="2">Two</option>
                                        <option value="3">Three</option>
                                    </select>
                                </div>
                                {errors.city && errors.city.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">City is required.</p>
                                )}
                            </div>

                            <div class="col-md-4">
                                <div class="my-3">
                                    <label htmlFor="zcode" class="form-label heading-color fw-bold">Zip code</label>
                                    <input type="number" name="zipcode" class="form-control" id="zcode" placeholder="Enter zipcode"
                                        {...register('zipcode', { required: true })} />
                                </div>
                                {errors.zipcode && errors.zipcode.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Zipcode is required.</p>
                                )}
                            </div>

                            <div class="col-md-8">
                                <div class="my-3">
                                    <label htmlFor="address" class="form-label heading-color fw-bold">Address</label>
                                    <input type="text" name="address" class="form-control" id="address" placeholder="Enter address"
                                        {...register('address', { required: true })} />
                                </div>
                                {errors.address && errors.address.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Address is required.</p>
                                )}
                            </div>

                            <div class="col-md-12">
                                <div class="my-3 text-area">
                                    <label htmlFor="exampleFormControlTextarea1" class="form-label heading-color fw-bold">About you</label>
                                    <textarea class="form-control" name="about" id="exampleFormControlTextarea1" rows="4"
                                        placeholder="Type something about you, experience, hobbies etc..."
                                        {...register('about', { required: true })}></textarea>
                                </div>
                                {errors.about && errors.about.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">About is required.</p>
                                )}
                            </div>
                        </div>
                        <div class="text-end account-details-save mt-3">
                            <button type="submit" class="btn btn-success">Save</button>
                        </div>
                    </form>
                </div>
            </div >
        </div>
        </div >

    );
}
const mapStateToProps = state => {
    return {
        user: state.user.user
    }
}
const mapDispatchToProps = dispatch => {
    return {
        saveUser: (payload) => dispatch(saveUser(payload))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AccountDetail)