import React, { useState, useEffect } from "react";
import { connect } from "react-redux"
import { saveUser } from "../redux/User/user.actions"
import AccountDetail from "./account-detail";
import UpdatePassword from "./update-password";
const { REACT_APP_API_URL } = process.env;

async function uploadPicture(picture) {
  let formData = new FormData();
  formData.append('picture', picture);
  return fetch(`${REACT_APP_API_URL}api/upload/user/picture`, {
    method: 'POST',
    headers: {
      'x-access-token': localStorage.getItem('token'),
    },
    body: formData
  });
}

function Profile(props) {
  const [imgData, setImgData] = useState(props.user.pictures == null ? 'user.png': `${REACT_APP_API_URL}${props.user.pictures}`);

  const onChangePicture = async e => {
    if (e.target.files[0]) {
      const reader = new FileReader();
      reader.addEventListener("load", () => {
        setImgData(reader.result);
      });
      reader.readAsDataURL(e.target.files[0]);

      await uploadPicture(e.target.files[0]).then(data => data.json())
        .then(res => {
          if (res.status == true) {
            localStorage.setItem('cuser', JSON.stringify(res.data));
            props.saveUser(res.data);
          }
        })
    }
  };
  return (

    <div>
      <div class="container">
        <div class="row">
          <div class="col-md-12">
            <div class="row">
              <div class="col-md-12">
                <div class="user-profile-wrapper">
                  <div class="profile-section">
                    <div class="profile-image">
                      <div class="inner-shadow">
                        <img className="playerProfilePic_home_tile" src={imgData} class="img-fluid" />
                      </div>
                    </div>
                  </div>
                  <div class="change-profile-icon">
                    <input class="d-none" id="selectImage" type="file" onChange={onChangePicture} />
                    <a href="#" onClick={() => document.getElementById("selectImage").click()}>
                      <i class="bi bi-camera-fill"></i>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <AccountDetail />
      <UpdatePassword />
    </div>
  );
}

const mapStateToProps = state => {
  return {
    user: state.user.user
  }
}
const mapDispatchToProps = dispatch => {
  return {
    saveUser: (payload) => dispatch(saveUser(payload))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Profile)