import React, { useState } from "react";
import { useForm } from "react-hook-form"
import { connect } from "react-redux"
import { saveUser } from "../redux/User/user.actions"
// import { Navigate,  } from "react-router-dom";
import { useNavigate } from "react-router-dom";


const { REACT_APP_API_URL } = process.env;

async function loginUser(credentials) {
   return fetch(`${REACT_APP_API_URL}api/login`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(credentials)
   });
}
function Login(props) {
   let navigate = useNavigate();
   const { watch, register, handleSubmit, setError, getValues, setValue, clearErrors, formState: { errors } } = useForm();
   
   const onSubmit = async e => {
      setValue('login_as', 'user', { shouldValidate: false, shouldDirty: false })
      const token = await loginUser(getValues()).then(data => data.json())
         .then(res => {
            if(res.status == false){
               setError("serverError", { type: "focus", message: res.message });
            }else{
               localStorage.setItem('token', res.data.token);
               localStorage.setItem('cuser', JSON.stringify(res.data));
               props.saveUser(res.data);
               navigate("/cook-list");
            }
         })
   }
   React.useEffect(() => { watch((value, { name, type }) => clearErrors('serverError')) }, [watch]);

   return (
      <div>
         <div class="container">
            <div class="row justify-content-center">
               <div class="col-md-6">
                  <div class="row justify-content-center">
                     <div class="col-md-10">
                        <div class="login-wrapper">
                           <h2 class="heading-color fw-bold mb-3">Login</h2>
                           <form onSubmit={handleSubmit(onSubmit)}>
                              <div class="mb-3">
                                 <div class="input-group">
                                    <input type="email" name="email" class="form-control" placeholder="Email address"
                                       aria-label="email" aria-describedby="basic-addon1"
                                       {...register('email', {
                                          required: true, pattern:
                                             /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,})+$/
                                       })} />
                                    <span class="input-group-text" id="basic-addon1"><i
                                       class="bi bi-envelope-fill text-color"></i></span>

                                 </div>
                                 {errors.email && errors.email.type === "required" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Email is required.</p>
                                 )}
                                 {errors.email && errors.email.type === "pattern" && (
                                    <p className="errorMsg mb-0 mt-2 text-danger">Email is not valid.</p>
                                 )}
                              </div>

                              <div class="mb-3">
                                 <div class="input-group">
                                    <input type="password" name="password" class="form-control" placeholder="Password"
                                       aria-label="password" aria-describedby="basic-addon1"
                                       {...register('password', { required: true, minLength: 6 })} />
                                    <span class="input-group-text" id="basic-addon1"><i
                                       class="bi bi-eye-slash-fill text-color"></i></span>
                                 </div>
                                 {errors.password && errors.password.type === "required" && (
                                    <p className="errorMsg mt-2 mb-0 text-danger">Password is required.</p>
                                 )}
                                 {errors.password && errors.password.type === "minLength" && (
                                    <p className="errorMsg mt-2 mb-0 text-danger">
                                       Password should be at-least 6 characters.
                                    </p>
                                 )}
                                 {errors.serverError && errors.serverError.type === "focus" && (
                                    <p className="errorMsg mt-2 mb-0 text-danger">{errors.serverError.message}</p>
                                 )}
                              </div>
                              <button type="submit" class="btn btn-success w-100 mt-2">Login</button>

                              <div class="forgot-password text-center mt-3">
                                 <a href="/cookreact/forgot">Forgot Password?</a>
                              </div>
                           </form>

                           <div class="text-center mt-4 sign-up-option">
                              <p>Don't have an account yet?<a href="/cookreact/signup"> Sign Up</a></p>
                          </div>

                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
   );
}
const mapStateToProps = () => ({})
const mapDispatchToProps = dispatch => {
   return {
     saveUser: (payload) => dispatch(saveUser(payload))
   }
 }
export default connect(mapStateToProps, mapDispatchToProps)(Login)
