import React from "react";
import slideImg1 from '../public/signup-success.png';

function SignupSuccess() {
return (

<div>
    <div class="container">
        <div class="row justify-content-center">
            <div class="col-md-6">
                <div class="row justify-content-center">
                    <div class="col-md-10">
                        <div class="signup-success-message text-center">
                            <img src={slideImg1} />
                            <h4 class="fw-bold heading-color mt-3">Your account created successfully</h4>
                            <h5 class="text-color mt-3">Please verify you mail to login.</h5>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
);
}

export default SignupSuccess;