import React from "react";
import { useForm } from "react-hook-form"
import { connect } from "react-redux"
import { saveUser } from "../redux/User/user.actions"
import { useNavigate } from "react-router-dom";
const { REACT_APP_API_URL } = process.env;

async function registerUser(details) {
    return fetch(`${REACT_APP_API_URL}api/register/${details.type}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(details)
    });
}
function Signup(props) {
    let navigate = useNavigate();
    const { watch, register, handleSubmit, setError, getValues, setValue, clearErrors, formState: { errors } } = useForm();

    const onSubmit = async e => {
        setValue('lat', '-104.9903', { shouldValidate: false, shouldDirty: false })
        setValue('lng', '39.7392', { shouldValidate: false, shouldDirty: false })
        const token = await registerUser(getValues()).then(data => data.json())
            .then(res => {
                if (res.status == false) {
                    setError("serverError", { type: "focus", message: res.message });
                } else {
                    localStorage.setItem('token', res.token);
                    localStorage.setItem('cuser', JSON.stringify(res));
                    props.saveUser(res);
                    navigate("/cook-list");
                }
            })
    }
    React.useEffect(() => { watch((value, { name, type }) => clearErrors('serverError')) }, [watch]);
    return (
        <div>
            <div class="container">
                <div class="row justify-content-center">
                    <div class="col-lg-6 col-md-8">
                        <div class="row justify-content-center">
                            <div class="col-md-10">
                                <div class="signup-wrapper">
                                    <h2 class="heading-color fw-bold mb-3">Sign up</h2>
                                    <form onSubmit={handleSubmit(onSubmit)}>
                                        <div class="row">
                                            <div class="col-md-6">
                                                <div class="mb-3">
                                                    <div class="input-group">
                                                        <input type="text" name="firstname" class="form-control" placeholder="First name" aria-label="fname"
                                                            aria-describedby="basic-addon1" {...register('firstname', { required: true })} />
                                                        <span class="input-group-text" id="basic-addon1"><i
                                                            class="bi bi-person-circle text-color"></i></span>
                                                    </div>
                                                    {errors.firstname && errors.firstname.type === "required" && (
                                                        <p className="errorMsg mb-0 mt-2 text-danger">Firstname is required.</p>
                                                    )}
                                                </div>
                                            </div>

                                            <div class="col-md-6">
                                                <div class="mb-3">
                                                    <div class="input-group">
                                                        <input type="text" name="lastname" class="form-control" placeholder="Last name" aria-label="lname"
                                                            aria-describedby="basic-addon1" {...register('lastname', { required: true })} />
                                                        <span class="input-group-text" id="basic-addon1"><i
                                                            class="bi bi-person-circle text-color"></i></span>
                                                    </div>
                                                    {errors.lastname && errors.lastname.type === "required" && (
                                                        <p className="errorMsg mb-0 mt-2 text-danger">Lastname is required.</p>
                                                    )}
                                                </div>
                                            </div>
                                        </div>
                                        <select class="form-select form-select" name="type" defaultValue=''
                                            aria-label="Default select example" {...register('type', { required: true })}>
                                            <option disabled value=''>Account type</option>
                                            <option value="user">User</option>
                                            <option value="cook">Cook</option>
                                        </select>
                                        {errors.type && errors.type.type === "required" && (
                                            <p className="errorMsg mb-0 mt-2 text-danger">Account type is required.</p>
                                        )}
                                        <div class="my-3">
                                            <div class="input-group">
                                                <input type="email" class="form-control" placeholder="Email address" aria-label="email"
                                                    aria-describedby="basic-addon1" name="email"
                                                    {...register('email', {
                                                        required: true, pattern:
                                                            /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,})+$/
                                                    })} />
                                                <span class="input-group-text" id="basic-addon1"><i class="bi bi-envelope-fill text-color"></i></span>
                                            </div>
                                            {errors.email && errors.email.type === "required" && (
                                                <p className="errorMsg mb-0 mt-2 text-danger">Email is required.</p>
                                            )}
                                            {errors.email && errors.email.type === "pattern" && (
                                                <p className="errorMsg mb-0 mt-2 text-danger">Email is not valid.</p>
                                            )}
                                        </div>
                                        <div class="my-3">
                                            <div class="input-group">
                                                <input type="password" name="password" class="form-control" placeholder="Password"
                                                    aria-label="password" aria-describedby="basic-addon1"
                                                    {...register('password', { required: true, minLength: 6 })} />
                                                <span class="input-group-text" id="basic-addon1"><i
                                                    class="bi bi-eye-slash-fill text-color"></i></span>
                                            </div>
                                            {errors.password && errors.password.type === "required" && (
                                                <p className="errorMsg mt-2 mb-0 text-danger">Password is required.</p>
                                            )}
                                            {errors.password && errors.password.type === "minLength" && (
                                                <p className="errorMsg mt-2 mb-0 text-danger">
                                                    Password should be at-least 6 characters.
                                                </p>
                                            )}
                                            {errors.serverError && errors.serverError.type === "focus" && (
                                                <p className="errorMsg mt-2 mb-0 text-danger">{errors.serverError.message}</p>
                                            )}
                                        </div>

                                        <div class="Signup-button">
                                            <button type="submit" class="btn btn-success w-100">Sign up</button>
                                        </div>
                                    </form>
                                    <div class="text-center mt-4 sign-up-option">
                                        <p>Already have an account?<a href="/cookreact/login"> Login</a></p>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

const mapStateToProps = () => ({})
const mapDispatchToProps = dispatch => {
    return {
        saveUser: (payload) => dispatch(saveUser(payload))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Signup)